.btn_standart {
  transition: all .2s;
  display: block;
  cursor: pointer;
  width: fit-content;
  letter-spacing: normal;
  font-weight: 600;
  font-size: 16px;
  letter-spacing: 2px;
  padding: 10px 25px;
  min-width: 170px;
  text-align: center;
  border-radius: 4px; }
  .btn_standart_red {
    color: #fff;
    border: solid 1px #DB1A1A;
    background: #DB1A1A;
    box-shadow: 0px 10px 30px rgba(56, 29, 219, 0.2); }
    .btn_standart_red:hover {
      border: 1px solid #333333;
      background: #333333; }
  .btn_standart_black {
    color: #fff;
    border: solid 1px transparent !important;
    background: #333333; }
    .btn_standart_black:hover {
      border: solid 1px #DB1A1A !important;
      background: #DB1A1A; }
    .btn_standart_black_2 {
      color: #595959;
      border: solid 1px #595959;
      background: transparent; }
      .btn_standart_black_2:hover {
        border: solid 1px #DB1A1A;
        background: #DB1A1A; }
