@charset "UTF-8";
.input {
  width: 100%;
  position: relative;
  margin: 30px 0; }
  .input_box {
    position: relative; }
  .input_label {
    font-stretch: normal;
    font-style: normal;
    letter-spacing: normal;
    font-weight: 400;
    position: absolute !important;
    top: 12px;
    left: 0;
    transition: .3s;
    font-size: 16px;
    line-height: 1; }
  .input-field {
    width: 100%;
    background: inherit;
    outline: none;
    border: none;
    font-size: 18px;
    line-height: 1;
    padding: 13px 0; }
  .input-black .input_label {
    color: #fff; }
  .input-black .input-field {
    padding-left: 10px;
    background: #2B2B2B;
    border: 1px solid #434343;
    border-radius: 4px;
    color: #fff; }
    .input-black .input-field:-webkit-autofill {
      -webkit-box-shadow: inset 0 0 0 50px #2B2B2B !important;
      /* Цвет фона */
      -webkit-text-fill-color: #fff !important;
      /* цвет текста */
      color: #fff !important;
      /* цвет текста */ }
    .input-black .input-field:-webkit-autofill:focus {
      -webkit-box-shadow: inset 0 0 0 50px #2B2B2B !important;
      /* Цвет фона */
      -webkit-text-fill-color: #fff !important;
      /* цвет текста */
      color: #fff !important;
      /* цвет текста */ }
  .input-white .input_label {
    color: #fff; }
  .input-white .input-field {
    color: #fff;
    border-bottom: 1px solid #fff; }
    .input-white .input-field:-webkit-autofill {
      -webkit-box-shadow: inset 0 0 0 50px #fff !important;
      /* Цвет фона */ }
    .input-white .input-field:-webkit-autofill:focus {
      -webkit-box-shadow: inset 0 0 0 50px #fff !important;
      /* Цвет фона */ }

.input_label {
  top: -10px;
  transition: .3s;
  font-size: 14px; }

.in-focus .input_label {
  top: -10px;
  transition: .3s;
  font-size: 14px; }

.is-error.in-focus .input_label {
  color: #ff4b4b; }

.is-error .input-field {
  border-bottom: 1px solid #ff4b4b;
  color: #ff4b4b; }

.is-error .input_label {
  color: #ff4b4b; }

.is-error label.error {
  font-size: 12px;
  font-weight: 500;
  line-height: 1.5;
  color: #ff4b4b; }

label.error {
  font-size: 12px;
  font-weight: 500;
  line-height: 1.5;
  color: #ff4b4b; }

.static_input {
  outline: none;
  border: none;
  color: #351717;
  width: 100%;
  background: #F2F2F2;
  border-radius: 6px;
  padding: 15px;
  margin-bottom: 5px; }
  .static_input__wrap {
    width: 300px; }
  .static_input:-webkit-autofill {
    -webkit-box-shadow: inset 0 0 0 50px #2B2B2B !important;
    /* Цвет фона */ }
  .static_input:-webkit-autofill:focus {
    -webkit-box-shadow: inset 0 0 0 50px #2B2B2B !important;
    /* Цвет фона */ }
